.App {
  text-align: center;

  display: flex;
  justify-content: center;
}

.App-header {
  min-height: 1vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: space-between;
  text-align: justify;
  text-align-last: justify;
 
  
}

.App-title{
  font-size: calc(24px + 2vmin);
  font-weight: bold;
  margin: 0%;
}



.App-link {
  color: #61dafb;
}

.spacer {
  flex: 1;
  min-width: 1rem;
  min-height: 1rem;

}


.body-content {
  margin-top: 2rem;
}

.top-cards {

  margin-bottom: 1rem;
  max-width: 70rem;
  display: flex;
  flex-direction: row;
  
  column-gap: 1rem;


}


.small-card {
  flex: 1;
}

.small-card-content {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  text-align: left;
}

.small-card-content-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;



}

.small-card-content-row-low {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;



}


.main-chart {
  max-width: 141rem;
  margin-bottom: 5rem;
}

.ButtonRow {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  
  


  justify-content: space-between;
}

.Alert{
  margin-bottom: 1rem;

}

.NestedRow {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}